import React, {Component} from 'react';
import './Alert.scss';

class Alert extends Component {
    state = {
        alertType: 'default'
    };

    render() {
        var className = "component-alert " + this.state.alertType;
        return (
            this.props.msg
                ? (
                    <div className={className}>
                        {this.props.msg}
                    </div>
                )
                : '');
    }
}

export default Alert;