import React from 'react';
import './Input.scss';
import './File.scss';
import Input from "./Input";
import uploadImage from "./../../Resources/Images/upload_icon.png";

class FileInput extends Input {
    static FILE_TYPE_DOCUMENT = 'document';
    static FILE_TYPE_IMAGE = 'image';

    state = {
        value     : '',
        options   : [],
        isDark    : false,
        invalid   : false,
        chosenFile: '',
        file      : null
    };

    constructor(props) {
        super(props);
        this.fileInput = React.createRef();
    }

    setFilename = (e) => {
        this.setFilenameFromInput(e.currentTarget);
    };

    getAcceptStringFromState = () => {
        let acceptString = '';

        switch (this.state.accept) {
            case FileInput.FILE_TYPE_IMAGE:
                acceptString = 'image/png, image/jpeg';
                break;
            case FileInput.FILE_TYPE_DOCUMENT:
                acceptString = '.doc,.docx,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document,.pdf';
                break;
            default:
                acceptString = '';
        }

        return acceptString;
    };

    handleLabelClick = () => {
        const inputId = this.state.id;
        const input = document.getElementById(inputId);
        input.value = '';
    };

    setFilenameFromInput(input) {
        const filepath = input.value;
        const filename = filepath.replace(/^.*[\\/]/, '');
        this.setState({
            chosenFile: filename,
            value     : filepath,
            file      : input.files[0],
        })
    }

    removeFile = (e) => {
        e.preventDefault();
        e.stopPropagation();
        this.setState({
            chosenFile: '',
            value     : '',
            file      : null,
        })
    };

    render() {
        let inputClass = (this.state.inputClass ? this.state.inputClass : '');
        inputClass += (this.props.isDark ? ' dark-bg' : '');
        inputClass += (this.state.invalid ? ' invalid' : '');

        const containerClass = "component-input file-input " + (this.state.showLabel ? ' show-label' : '');
        const acceptString = this.getAcceptStringFromState();

        var labelClass = this.state.chosenFile ? 'chosen' : '';
        labelClass += this.state.invalid ? ' invalid' : '';

        return (
            <div className={containerClass}>
                <label
                    htmlFor={this.props.id}
                    title={this.state.chosenFile}
                    className={labelClass}
                    onClick={this.handleLabelClick}
                >
                    {this.state.chosenFile
                        ?
                        <span className="delete-file-button" onClick={this.removeFile}>&times;</span>
                        : <img src={uploadImage} className="upload-file-button" alt="Upload file"/>
                    }
                    {this.state.chosenFile ? this.state.chosenFile : (this.state.label ? this.state.label : 'Choose file')}
                </label>

                <input
                    className={inputClass}
                    id={this.state.id}
                    name={this.state.name}
                    type='file'
                    required={this.state.required}
                    onChange={this.setFilename}
                    accept={acceptString}
                    ref={this.fileInput}
                />
                <div className="file-input-note">{this.props.note ? this.props.note : "\u00a0"}</div>
            </div>
        );
    }
}

export default FileInput;