import React, {Component} from 'react';
import Footer from '../Components/Footer/Footer';
import InnerPagesNav from '../Components/InnerPagesNav/InnerPagesNav';
import './ContentPage.scss';
import SixGridImg from '../Components/EventsPage/SixGridImg';
import Infopack from '../Components/Infopack/Infopack';
import ThreeSteps from '../Components/EventsPage/ThreeSteps';
import FourImg from '../Components/EventsPage/FourImg';
import EventsHeader from '../Components/EventsPage/EventsHeader';
import MetaTags from 'react-meta-tags';

class Events extends Component {
    componentDidMount() {
        setTimeout(function() {
            window.scrollTo(0, 0);
        }, 500);
    }
    render() {
        return (
            <div className="events-page-component">
                <MetaTags>
                    <title>Event Management - SixthSense Agency</title>
                    <meta name="description" content="Event management with SixthSense Agency uses the power of experience and intuition to bring you a seamless event experience that will set your senses alive."/>
                </MetaTags>

                <div className="content-container-component">
                    <InnerPagesNav/>
                </div>
                <div className="content-container-full">
                    <EventsHeader/>
                </div>
                <div className="content-container-component">
                    <ThreeSteps/>
                    <SixGridImg/>
                    <FourImg/>
                    <Infopack text="Enliven your senses with SixthSense Agency"/>
                    <Footer/>
                </div>
            </div>
        );
    }
}

export default Events;