import React, {Component} from 'react';
import './Jobs.scss';
import Input from "../InputFields/Input";
import TextArea from "../InputFields/TextArea";
import FileInput from "../InputFields/FileInput";
import SubmitButton from "../InputFields/SubmitButton";
import Select from "../InputFields/Select";
import OpenPositions from "../JobsPage/OpenPositions";
import Config from "../../Config/Config";
import DangerAlert from "../Alerts/DangerAlert";
import SuccessAlert from "../Alerts/SuccessAlert";

class JobsForm extends Component {

    state = {
        isValidForm: true,
        disabled   : false,
        sending    : false
    };

    constructor(props) {
        super(props);
        this.NameInput = React.createRef();
        this.PhoneInput = React.createRef();
        this.EmailInput = React.createRef();
        this.MessageInput = React.createRef();
        this.PositionsInput = React.createRef();
        this.PersonalPhotoInput = React.createRef();
        this.HobbyPhotoInput = React.createRef();
        this.AgeInput = React.createRef();
        this.ResumeInput = React.createRef();
        this.VisaStatusInput = React.createRef();
    }

    submitForm = (event) => {
        let valid = this.validateForm();
        event.preventDefault();
        if (valid) {
            this.setState({waiting: true});
            var data = {
                name         : this.NameInput.current.state.value,
                email        : this.EmailInput.current.state.value,
                phone        : this.PhoneInput.current.state.value,
                age          : this.AgeInput.current.state.value,
                message      : this.MessageInput.current.state.value,
                position     : this.PositionsInput.current.state.value,
                resume       : this.ResumeInput.current.state.file,
                hobbyPhoto   : this.HobbyPhotoInput.current.state.file,
                personalPhoto: this.PersonalPhotoInput.current.state.file,
                visaStatus   : this.VisaStatusInput.current.state.value,
            };

            var formData = new FormData();

            var url = Config.api.baseUrl + Config.api.jobsSlug;
            for (var fieldName in data) {
                var fieldValue = data[fieldName];
                formData.append(fieldName, fieldValue);
            }

            fetch(url, {
                method: 'POST',
                body  : formData,
            })
                .then(response => response.json())
                .then(response => {
                    this.setState({sending: false});
                    if (response.success === true) {
                        this.setSuccessMessage(response.message ? response.message : 'Your message was sent. Thank you.');
                        this.setState({disabled: true})
                    } else {
                        this.setState({disabled: false});
                        this.setErrorMessage(response.message ? response.message : 'There was an unexpected error. Please try again.');
                    }
                })
                .catch(() => {
                    this.setState({sending: false});
                    this.setState({waiting: false});
                    this.setState({disabled: false});
                    this.setErrorMessage('There was an unexpected error. Please try again. If the problem persists, please contact us via our contact form. You can find the contact form in the footer or on the main navigation of this page.');
                });
            this.setState({sending: true});
            this.setState({disabled: true});
        }
    };

    validateForm = () => {
        this.resetErrorMessage();

        var name = this.NameInput.current.state,
            email = this.EmailInput.current.state,
            phone = this.PhoneInput.current.state,
            message = this.MessageInput.current.state,
            position = this.PositionsInput.current.state,
            resume = this.ResumeInput.current.state,
            personalPhoto = this.PersonalPhotoInput.current.state,
            visaStatus = this.VisaStatusInput.current.state;

        name.invalid = false;
        email.invalid = false;
        phone.invalid = false;
        message.invalid = false;
        position.invalid = false;
        resume.invalid = false;
        personalPhoto.invalid = false;
        visaStatus.invalid = false;

        if (this.state.disabled) {
            this.setErrorMessage('A request is already being sent, please wait a bit longer. If you feel this is an error, refresh the page and try again.');
            return false;
        }

        if (!position.value || position.value === '') {
            this.setErrorMessage('Please specify what position you are interested in. If you are interested in multiple positions, please say so in the message.');
            position.invalid = true;
            return false;
        }

        if (!name.value || name.value === '') {
            this.setErrorMessage('Your name is required');
            name.invalid = true;
            return false;
        }

        if (!email.value || email.value === '') {
            this.setErrorMessage('Your email is required so we can get back to you');
            email.invalid = true;
            return false;
        }

        if (!phone.value || phone.value === '') {
            this.setErrorMessage('Your phone is required');
            phone.invalid = true;
            return false;
        }

        if (!resume.value || resume.value === '') {
            this.setErrorMessage('Please attach an english version of your resume, in PDF or WORD format');
            resume.invalid = true;
            return false;
        }

        if (!personalPhoto.value || personalPhoto.value === '') {
            this.setErrorMessage('Please attach a personal photo of yourself');
            personalPhoto.invalid = true;
            return false;
        }

        if (!visaStatus.value || visaStatus.value === '') {
            this.setErrorMessage('Please specify what your current visa or citizenship status is in Australia');
            visaStatus.invalid = true;
            return false;
        }

        if (!message.value || message.value === '') {
            this.setErrorMessage('Please tell us something about yourself in the message. Consider it a cover letter');
            message.invalid = true;
            return false;
        }

        return true;
    }

    setErrorMessage(message) {
        this.setState({
            isValidForm : false,
            errorMessage: message
        });
    }

    setSuccessMessage(message) {
        this.setState({
            isValidForm   : true,
            successMessage: message
        });
    }

    resetErrorMessage() {
        this.setState({
            isValidForm   : true,
            errorMessage  : '',
            successMessage: ''
        });
    }

    render() {
        var positions = [
            'Boardroom waiter',
            'Event waiter',
            'Bartender',
            'Mixologist',
            'Barista',
            'Headwaiter',
        ];
        var visaOptions = [
            'Citizen',
            'Permanent Resident',
            'Temporary Resident',
            'Working Holiday',
            'International Student',
            'Other (Please specify in your message)',
        ];

        var buttonText = this.state.sending ? "Sending" : "Submit";

        return (
            <div>
                <OpenPositions positions={positions} />
                <form className="component-jobs-form" onSubmit={this.submitForm} >
                    <div className="title">
                        <h2>Apply now</h2>
                        <div className="gold-bar"/>
                    </div>

                    <Select
                        options={positions}
                        required={true}
                        label="Choose a position *"
                        ref={this.PositionsInput}
                    />

                    <div className="small-fields">
                        <div className="small-field">
                            <Input
                                placeholder="Full name *"
                                autocomplete="name"
                                name="name"
                                required={true}
                                ref={this.NameInput}
                            />
                        </div>

                        <div className="small-field">
                            <Input
                                placeholder="Age"
                                autocomplete="age"
                                type="number"
                                name="age"
                                ref={this.AgeInput}
                            />
                        </div>

                        <div className="small-field">
                            <Input
                                placeholder="E-mail *"
                                autocomplete="email"
                                type="email"
                                name="email"
                                required={true}
                                ref={this.EmailInput}
                            />
                        </div>

                        <div className="small-field">
                            <Input
                                placeholder="Phone *"
                                autocomplete="tel"
                                type="tel"
                                name="phone"
                                required={true}
                                ref={this.PhoneInput}
                            />
                        </div>
                    </div>

                    <FileInput
                        id="resume-file"
                        label="Upload your resume *"
                        name="resume"
                        accept={FileInput.FILE_TYPE_DOCUMENT}
                        note="PDF or DOCX format (Max. 2MB)"
                        required={true}
                        ref={this.ResumeInput}
                    />


                    <FileInput
                        id="photo-file-2"
                        label="Select 1 photo of you *"
                        name="photo-2"
                        accept={FileInput.FILE_TYPE_IMAGE}
                        required={true}
                        ref={this.PersonalPhotoInput}
                    />

                    <FileInput
                        id="photo-file-1"
                        label="Choose a photo of your favourite hobby"
                        name="photo-1"
                        accept={FileInput.FILE_TYPE_IMAGE}
                        ref={this.HobbyPhotoInput}
                    />

                    <Select
                        options={visaOptions}
                        required={true}
                        label="Visa/Citizenship status *"
                        ref={this.VisaStatusInput}
                    />

                    <TextArea
                        autocomplete="off"
                        rows="4"
                        placeholder="Message *"
                        name="message"
                        ref={this.MessageInput}
                        required={true}
                    />

                    {
                        !this.state.isValidForm
                        ? <DangerAlert msg={this.state.errorMessage}/>
                        : <SuccessAlert msg={this.state.successMessage}/>
                    }

                    <SubmitButton
                        disabled={this.state.disabled}
                        label={buttonText}
                    />
                </form>
            </div>
        );
    }
}

export default JobsForm;