import React, {Component} from 'react';
import './ImageHover.scss';



class ImageHover extends Component {
    state={};

    componentDidMount() {
        this.setState(this.props);  
    }

    render() {
        const style = {backgroundImage: "url(" + this.state.imageSrc + ")"};

        return (
            <div className="image-hover-component">
                <div className="image-container">
                    <div style={style} className="image-hover">
                        <img src={this.state.imageSrc} alt=""/>
                    </div>
                    
                    <div className="text-container">
                        <p className="image-text">{this.state.text}</p>
                    </div>
                </div>
            </div>
        );
    }
}

export default ImageHover;