import React, {Component} from 'react';
import './StartChat.scss';
import Links from '../../Config/Links';
import { Link } from 'react-router-dom';


class StartChat extends Component {
    render() {
        return(
            <div className="start-chat-component">
                <h2 className="title">Start A Conversation with SixthSense Today</h2>
                <p className="text">If youre starting to plan an event and want to ensure you have the best possible waitstaff, <Link to ={Links.contact}>contact</Link> the team at SixthSense Agency. <br/>Well bring you waitstaff that is a match made in heaven.</p>
            </div>
        );
    }
}

export default StartChat;