import React, {Component} from 'react';
import './StaffHeader.scss';
import Title from "../Misc/Title";
import Background from "../../Resources/Images/staff-sq.png";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";

class StaffHeader extends Component {
    render() {
        return (

            <div className="staff-header-component">
                <div className="content-container" style={{backgroundImage: "url(" + Background + ")"}}>
                    <div className="title">
                        <Title text="Meet Your Perfect Match"/>
                    </div>

                    <p className="text">
                        Finding the right staff to ensure the success of your
                        <Link to={Links.events}> event</Link> can be an overwhelming
                        and stressful process. At SixthSense Agency, we understand
                        the time and effort that goes into pulling off a successful
                        event  which is why we have staff that are suitably trained
                        to provide exceptional hospitality services.
                    </p>
                </div>
            </div>

        );
    }
}

export default StaffHeader;