import React, {Component} from 'react';
import './MiniSixSquaresLogo.scss';
import SixLines from "../SixLines/SixLines"
import LogoSixthSenseText from "./LogoSixthSenseText"
import LogoAgencyText from "./LogoAgencyText"


class MiniSixSquaresLogo extends Component {
    render() {
        return (
            <div className="component-mini-six-squares-logo">
                <div className="mini-six-squares">
                    <SixLines horizontal={false} bg_dark={this.props.mode === 'dark'}/>
                    <SixLines horizontal={true} bg_dark={this.props.mode === 'dark'}/>
                    <SixLines horizontal={false} bg_dark={this.props.mode === 'dark'} accent={'all'}/>
                    <SixLines horizontal={true} bg_dark={this.props.mode === 'dark'}/>
                    <SixLines horizontal={false} bg_dark={this.props.mode === 'dark'}/>
                    <SixLines horizontal={true} bg_dark={this.props.mode === 'dark'}/>
                </div>

                <LogoSixthSenseText color1={this.props.mode === 'dark' ? 'white' : 'black'}/>

                <LogoAgencyText/>
            </div>
        );
    }
}

export default MiniSixSquaresLogo;