import React, {Component} from 'react';
import './CesarContent.scss';
import CesarPhoto from "../../Resources/Images/CesarImage.jpg";
import { Link } from 'react-router-dom';
import Links from '../../Config/Links';
import Submit from "../InputFields/SubmitButton";




class CesarContent extends Component {


    render() {
        return (
            <div className="cesar-content-component">
                <div className="image-left-container">
                    <img src={CesarPhoto} alt="Founder of the company" className="image-left"/>
                </div>
                <div className="text-right-container">
                    <h2 className="title">Who are <br/>SixthSense Agency?</h2>
                    <p className="text">
                        Over a highly successful career spanning more than 16 years,
                        owner Cesar Ramirez has mastered every hospitality role,
                        from hotelier to corporate hospitality, training and development,
                        management, and food &amp; beverage service. Specialising
                        in offering 5-star hospitality experiences to clients, he
                        is always searching for that extra 1% to elevate <Link to={Links.events}>events</Link>.
                        He is passionate about facing challenges head on and thrives
                        on the excitement of hosting a spectacular event, even under pressure.
                    </p>
                    <img src={CesarPhoto} alt="Founder of the company" className="image-half"/>
                    <Link to = {Links.contact}>
                        <Submit
                            label="Contact"
                            isDark={false}
                        />
                    </Link>
                </div>
                
            </div>
        );
    }
}

export default CesarContent;