import React, {Component} from 'react';
import './imageWithText.scss';

class ImageWithTextLTR extends Component {
	render() {
		return (
				<div className="image-with-text-component ltr">
					<div
							style={{backgroundImage: `url(${this.props.src})`}}
							className="image non-mobile animateme scrollme"
							title={this.props.title}
							data-when="enter"
							data-from="0.5"
							data-to="0.2"
							data-scale="0.7"
							data-easing="easeout"
							data-opacity="0"
					/>

					<div className="text">
						<h3
								className="animateme scrollme"
								data-when="exit"
								data-from="0.7"
								data-to="0.1"
								data-easing="easeout"
								data-opacity="0">{this.props.title}</h3>
						<p
								className="animateme scrollme"
								data-when="exit"
								data-from="0.3"
								data-to="0"
								data-easing="easeout"
								data-opacity="0"
								dangerouslySetInnerHTML={{__html: this.props.body}}
						/>

						{this.props.body2
						 ? <p
								 className="animateme scrollme"
								 data-when="exit"
								 data-from="0.3"
								 data-to="0"
								 data-easing="easeout"
								 data-opacity="0"
								 dangerouslySetInnerHTML={{__html: this.props.body2}}
						 />
						 : ''}
					</div>

					<div
							style={{backgroundImage: `url(${this.props.src})`}}
							title={this.props.title}
							className="image mobile animateme scrollme"
							data-when="enter"
							data-from="0.5"
							data-to="0.2"
							data-scale="0.7"
							data-easing="easeout"
							data-opacity="0"
					/>
				</div>
		)
	}
}

export default ImageWithTextLTR;