import React, {Component} from 'react';
import './HorizontalGallery.scss';


class HorizontalGallery extends Component {

    onClick = (e) => {
        if (typeof this.props.onClick === 'function') {
            this.props.onClick(e.currentTarget.dataset.index);
        }
    };

    render(){
        return (
            <div className="horizontal-gallery-component">
                <div className="five-container">
                    {
                        this.props.images.map((image, index) => {
                            return (
                                    <div className="image-container"
                                         style={{backgroundImage: "url(" + image.direct_link + ")"}}
                                         onClick={this.onClick}
                                         data-index={index}
                                    >
                                        <svg viewBox="0 0 1 1" className="svg">
                                            <rect
                                                    width="1"
                                                    height="1"
                                                    fill="none"
                                            />
                                        </svg>
                                    </div>
                            );
                        })
                    }

                </div>
            </div>
        )
    }
}
export default HorizontalGallery;