import React, {Component} from 'react';
import './StaffSplashImage.scss';

class StaffSplashImage extends Component {
    state={};

    componentDidMount() {
        this.setState(this.props);  
    }

    render() {
        return (
            <div className="component-staff-splash-image"> 
               <div className="empty-square-container">
                   <svg viewBox="0 0 51 51">
                        <g>
                            <rect
                                    className="empty-square"
                                    width="51"
                                    height="51"
                                    x="0"
                                    y="0"/>
                        </g>
                    </svg>
                </div>

                <div className="image-container">
                    <img src={this.state.imageSrc} alt={this.state.imageAlt} className="image-splash"></img>
                </div>

                <div className="svg-container">
                    <svg viewBox="0 0 51 51">
                        <g>
                            <rect
                                    className="default-bg"
                                    width="24"
                                    height="6"
                                    x="0"
                                    y="0"/>
                            <rect
                                    className="accent-bg"
                                    width="51"
                                    height="6"
                                    x="0"
                                    y="9" title="Jobs"/>
                            <rect
                                    className="default-bg"
                                    width="51"
                                    height="6"
                                    x="0"
                                    y="18"/>
                            <rect
                                    className="default-bg"
                                    width="51"
                                    height="6"
                                    x="0"
                                    y="27"/>
                            <rect
                                    className="default-bg"
                                    width="51"
                                    height="6"
                                    x="0"
                                    y="36"/>
                            <rect
                                    className="default-bg"
                                    width="51"
                                    height="6"
                                    x="0"
                                    y="45"/>
                        </g>
                    </svg>
                
                </div>
                
               
            </div>
        );
    }
}

export default StaffSplashImage;