import React, {Component} from 'react';
import './SixImages.scss';
import Six1 from "../../Resources/Images/bartender.jpg";
import Six2 from "../../Resources/Images/staff-six2.jpg";
import Six3 from "../../Resources/Images/staff-six3.jpg";
import Six4 from "../../Resources/Images/staff-six4.jpg";
import Six5 from "../../Resources/Images/staff-six5.jpg";
import Six6 from "../../Resources/Images/staff-six6.jpg";
import ImageHover from './ImageHover';

class SixImages extends Component {
    render() {
        return (
            <div className="six-images-component">
                <div className="top">
                    <div>
                        <ImageHover
                            text="Bar Staff"
                            imageSrc={Six1}
                        />    
                    </div>

                    <div>
                        <ImageHover
                            text="Barista"
                            imageSrc={Six2}
                        />    
                    </div>
                    <div>
                        <ImageHover
                            text="Cocktail waiter"
                            imageSrc={Six3}
                        />    
                    </div>
                    <div>
                        <ImageHover
                            text="Boardroom"
                            imageSrc={Six4}
                        />
                    </div>
                    <div>  
                        <ImageHover
                            text="Supervisors"
                            imageSrc={Six5}
                        />
                    </div>  
                    <div> 
                        <ImageHover
                            text="Mixologist"
                            imageSrc={Six6}
                        />
                    </div>   
                </div>
                
            </div>
        );
    }
}

export default SixImages;