import React, {Component} from 'react';
import './EventsSplash.scss';
import EventsSplashImage from './EventsSplashImage';
import BaseSplash from "../SplashPages/BaseSplash";
import Submit from "../InputFields/SubmitButton";
import SPEvents from "../../Resources/Images/sp-events.jpg";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";
import MetaTags from "react-meta-tags";



class EventsSplash extends Component {
    render() {
        return(
            <BaseSplash>
                <MetaTags>
                    <link rel="canonical" href={Links.baseUrl + Links.events} />
                </MetaTags>
                <div className="component-events-splash">
                    <div className="image-container">
                        <EventsSplashImage
                            imageSrc={SPEvents}
                            imageAlt="Waiter in unifrom fixing a table with flowers"/>
                    </div>

                    <div className="text-container">
                        <div className="title">Event <br/>Management</div>
                        <p className="text">Experience truly personal event management with 
                            SixthSense Agency. We bring you a seamless event 
                            experience that will set your senses alive. </p>
                        <Link to={Links.events}>
                            <Submit
                                label="Learn More"
                                isDark={true}
                            />
                        </Link>
                    </div>
                </div>
            </BaseSplash>
        )
    }
}
export default EventsSplash;