import React from "react";
import {BrowserRouter, Route, Switch} from "react-router-dom";
import About from "../Controllers/About";
import Jobs from "../Controllers/Jobs";
import Staff from "../Controllers/Staff";
import Events from "../Controllers/Events";
import Memories from "../Controllers/Memories";
import './Vars.scss';
import './MainStyles.scss';
import './Animations.scss';
import Links from './Links';
import EventsSplash from "../Components/EventsPage/EventsSplash";
import JobsSplash from "../Components/JobsPage/JobsSplash";
import StaffSplash from "../Components/StaffPage/StaffSplash";
import AboutSplash from "../Components/AboutPage/AboutSplash";
import MemoriesSplash from "../Components/MemoriesPage/MemoriesSplash";
import {TransitionGroup, CSSTransition} from 'react-transition-group'
import ContactSplash from "../Components/ContactPage/ContactSplash";
import ImagePreloader from "../Components/Misc/ImagePreloader";
import Experience from "../Controllers/Experience"


function Router() {
    return (
        <BrowserRouter>
            <Route render={({location}) => (
                <TransitionGroup>
                    <CSSTransition
                        key={location.key}
                        classNames='slide-down-up'
                        timeout={600}
                    >
                        <Switch location={location}>
                            <Route exact path={Links.jobsSplash} component={JobsSplash}/>
                            <Route exact path={Links.staffSplash} component={StaffSplash}/>
                            <Route exact path={Links.aboutSplash} component={AboutSplash}/>
                            <Route exact path={Links.eventsSplash} component={EventsSplash}/>
                            <Route exact path={Links.memoriesSplash} component={MemoriesSplash}/>
                            <Route exact path={Links.contact} component={ContactSplash}/>
                        </Switch>
                    </CSSTransition>
                </TransitionGroup>
            )}/>

            <Route render={({location}) => (
                <TransitionGroup>
                    <CSSTransition
                        key={location.key}
                        classNames='none'
                        timeout={600}
                    >
                        <Switch location={location}>
                            <Route exact path={Links.home} component={Experience}/>
                        </Switch>
                    </CSSTransition>
                </TransitionGroup>
            )}/>

            <Route render={({location}) => (
                <TransitionGroup>
                    <CSSTransition
                        key={location.key}
                        classNames='fade-in-out'
                        timeout={600}
                    >
                        <Switch location={location}>
                            <Route exact path={Links.jobs} component={Jobs}/>
                            <Route exact path={Links.staff} component={Staff}/>
                            <Route exact path='/' component={Experience}/>
                            <Route exact path={Links.about} component={About}/>
                            <Route exact path={Links.events} component={Events}/>
                            <Route exact path={Links.memories} component={Memories}/>
                            <Route exact path={Links.experience} component={Experience}/>
                        </Switch>
                    </CSSTransition>
                </TransitionGroup>
            )}/>
            <ImagePreloader />
        </BrowserRouter>
    );
}

export default Router;