import React, {Component} from 'react';
import './SixLines.scss';

class SixLines extends Component {
    state = {
        horizontal: true,
        bg_dark   : true,
        accent    : false,
    };

    componentDidMount() {
        this.setState(this.props);
    }

    render() {
        var background = (this.state.bg_dark ? 'white' : 'black');
        var direction = (this.state.horizontal ? 'horizontal' : 'vertical');

        return (
            <div className="component-six-lines">
                <div className={direction}>
                    <svg viewBox="0 0 51 51">
                        <g>
                            <rect
                                className={this.state.accent === "1" || this.state.accent === 'all' ? background + ' accent' : background}
                                width="51"
                                height="6"
                                x="0"
                                y="0"/>
                            <rect
                                className={this.state.accent === "2" || this.state.accent === 'all' ? background + ' accent' : background}
                                width="51"
                                height="6"
                                x="0"
                                y="9"/>
                            <rect
                                className={this.state.accent === "3" || this.state.accent === 'all' ? background + ' accent' : background}
                                width="51"
                                height="6"
                                x="0"
                                y="18"/>
                            <rect
                                className={this.state.accent === "4" || this.state.accent === 'all' ? background + ' accent' : background}
                                width="51"
                                height="6"
                                x="0"
                                y="27"/>
                            <rect
                                className={this.state.accent === "5" || this.state.accent === 'all' ? background + ' accent' : background}
                                width="51"
                                height="6"
                                x="0"
                                y="36"/>
                            <rect
                                className={this.state.accent === "6" || this.state.accent === 'all' ? background + ' accent' : background}
                                width="51"
                                height="6"
                                x="0"
                                y="45"/>
                        </g>
                    </svg>

                </div>

            </div>
        )
    }
}

export default SixLines;