import React, {Component} from 'react';
import './InnerPagesNav.scss';
import Links from '../../Config/Links';
import {Link, NavLink} from 'react-router-dom';
import Hamburger from "../../Resources/Images/hamburger-black.png";
import Times from "../../Resources/Images/times-black.png";
import MiniSixSquaresLogo from "../Logo/MiniSixSquaresLogo"
import Config from "../../Config/Config"
import BookExperienceButton from "../ExperiencePage/BookExperienceButton"

class InnerPagesNav extends Component {
    state = {
        closed: true,
    };

    triggerClosedNavigation = () => {
        this.setState({closed: !this.state.closed});
    };

    render() {
        var containerClass = "component-inner-navigation" + (this.state.closed ? ' closed' : '') + (this.props.mode === 'dark' ? ' dark' : '');
        var icon = (
            this.state.closed ?
                <img src={Hamburger} alt="hambuger-icon"/> :
                <img src={Times} alt="exit navigation button" className="times"/>
        );

        return (
            <nav className={containerClass}>
                <div className="logo-container">
                    <Link to={Links.home}>
                        <MiniSixSquaresLogo mode={this.props.mode}/>
                    </Link>
                </div>

                <div className="open-close-button-container">
                    <div className={"open-close-button" + (this.props.mode === 'dark' ? ' dark' : '')} onClick={this.triggerClosedNavigation}>
                        {icon}
                    </div>
                </div>

                <div className="links-container">
                    <ul className="links-list">
                        <li className="link-container book-now-container">
                            <BookExperienceButton/>
                        </li>
                        {Config.navigation.map((link) => {
                            return (
                                    <li className="link-container" key={link.link}>
                                        <NavLink className="navigation-link" to={link.link}>
                                            {link.text}
                                        </NavLink>
                                    </li>
                            );
                        })}
                    </ul>
                </div>
            </nav>
        );
    }
}

export default InnerPagesNav;