import React, {Component} from 'react';
import './ContactSplash.scss';
import {Link} from 'react-router-dom';
import Links from '../../Config/Links';
import SixLines from "../SixLines/SixLines";
import Config from "../../Config/Config";
import ContactForm from "../ContactInfo/ContactForm";
import BaseSplash from "../SplashPages/BaseSplash";
import MetaTags from "react-meta-tags";


class ContactSplash extends Component {
    render() {
        return (
            <BaseSplash>
                <MetaTags>
                    <title>Contact - SixthSense Agency</title>
                    <meta name="description" content="Contact SixthSense Agency to receive a quote for your event, or if you are interested in working with us."/>
                </MetaTags>

                <div className="component-contact-splash">
                    <div className="text-container">
                        <div className="logo-container">
                            <Link to={Links.home}>
                                <SixLines accent="6"/>
                            </Link>
                        </div>

                        <div className="info-container">
                            <h1>Contact</h1>
                            <p>
                                If you are interested in making your next event
                                an unforgettable experience, do not hesitate to
                                connect with our hospitality connoisseur today.
                            </p>

                            <ul>
                                <li>
                                    <a href={"mailto:" + Config.contact.email}>
                                        {Config.contact.email}
                                    </a>
                                </li>

                                <li>
                                    <a href={"tel:" + Config.contact.phone}>
                                        {Config.contact.phone}
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>

                    <div className="form-container">
                        <ContactForm isDark={true}/>
                    </div>
                </div>
            </BaseSplash>
        )
    }
}

export default ContactSplash;