import React, {Component} from 'react';
import './MemoriesGallery.scss';
import './Gallery.scss';
import SvgBgImage from "./SvgBgImage";
import FullScreenImage from "./FullScreenImage";
import spinnerGif from './../../Resources/Images/spinner.gif';


class Gallery extends Component {

    state = {
        activeImage: -1
    };

    maximizeImage = (e, image) => {
        var index = this.props.images.indexOf(image);
        if (index !== -1) {
            this.setState({
                activeImage: index
            });
        }
    };

    closeImage = () => {
        this.setState({
            activeImage: -1
        });
    };


    render() {
        var images = this.props.images;

        return (
            <div style={{textAlign:'center'}}>
                {
                    images.length ? (
                        <section className="component-gallery-11-elements">
                            {images[0] ? <SvgBgImage image={images[0]} aspectRatio={2} onClick={this.maximizeImage}/> : ''}
                            {images[1] ? <SvgBgImage image={images[1]} aspectRatio={1} onClick={this.maximizeImage}/> : ''}
                            {images[2] ? <SvgBgImage image={images[2]} aspectRatio={1} onClick={this.maximizeImage}/> : ''}
                            {images[3] ? <SvgBgImage image={images[3]} aspectRatio={1} onClick={this.maximizeImage}/> : ''}
                            {images[4] ? <SvgBgImage image={images[4]} aspectRatio={1} onClick={this.maximizeImage}/> : ''}
                            {images[5] ? <SvgBgImage image={images[5]} aspectRatio={1.5} onClick={this.maximizeImage}/> : ''}
                            {images[6] ? <SvgBgImage image={images[6]} aspectRatio={1.5} onClick={this.maximizeImage}/> : ''}
                            {images[7] ? <SvgBgImage image={images[7]} aspectRatio={1} onClick={this.maximizeImage}/> : ''}
                            {images[8] ? <SvgBgImage image={images[8]} aspectRatio={1} onClick={this.maximizeImage}/> : ''}
                            {images[9] ? <SvgBgImage image={images[9]} aspectRatio={1} onClick={this.maximizeImage}/> : ''}
                            {images[10] ? <SvgBgImage image={images[10]} aspectRatio={1} onClick={this.maximizeImage}/> : ''}
                            {images[11] ? <SvgBgImage image={images[11]} aspectRatio={2} onClick={this.maximizeImage}/> : ''}
                            {this.state.activeImage !== -1 ? <FullScreenImage image={this.state.activeImage} onClose={this.closeImage} images={this.props.images}/> : ''}
                        </section>
                    ) : <img src={spinnerGif} alt="loading" style={{maxWidth:'7rem'}}/>
                }
            </div>
        );
    }
}

export default Gallery;