import React, {Component} from 'react';
import './EventsHeader.scss';
import Title from "../Misc/Title";
import RWImage from "../../Resources/Images/red-wine.jpg";
import Background from "../../Resources/Images/events-sq1.png";


class EventsHeader extends Component {
    render() {
        return (

            <div className="events-header-component">
                <div className="top-container" style={{backgroundImage: "url(" + Background + ")"}}>
                    <div className="title">
                        <Title text="Personal Event Management"/>
                        <p className="subtitle">From Start to Finish</p>   
                    </div>

                    <div className="image-container">
                        <img src={RWImage} alt="a person pouring red wine in a glass"/>
                    </div>
                </div>
               
                <div className="text-container">
                    <p className="text">The creative ideas behind an event are only the first step in planning and hosting a seamless experience. With extensive inside knowledge of the hospitality industry, the event management team at SixthSense Agency know the secrets to creating magical events. </p>
                    
                    <div className="image-container-bottom">
                        <img src={RWImage} alt="a person pouring red wine in a glass" className="img-bottom"/>
                    </div>

                    <p className="text2">Our core philosophy is creating personalised events with intuition using our "Sixth Sense" for hospitality so that clients and guests' needs are consistently met and exceeded.</p>

                </div>

            </div>

        );
    }
}

export default EventsHeader;