import React, {Component} from 'react';
import './MemoriesHeader.scss';
import Title from "../Misc/Title";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";


class MemoriesHeader extends Component {
    render() {
        return (
            <div className="component-memories-header">
                <div className="title">
                    <Title text="When was the last event that made you smile?"/>
                </div>
                <p className="extra-text">
                    Every <Link to={Links.events}>event</Link> in your life is an opportunity to create new memories.
                    <br/>
                    If you can make people smile you can find yourself a place
                    within their heart and mind.
                </p>
            </div>
        );
    }
}

export default MemoriesHeader;