import React, {Component} from 'react';
import './MemoriesGallery.scss';
import './SvgBgImage.scss';

class SvgBgImage extends Component {

    handleOnClick = (e) => {
        if (typeof this.props.onClick === 'function') {
            this.props.onClick(e, this.props.image);
        }
    };

    render() {
        return (
            <figure style={{backgroundImage: "url(" + this.props.image.direct_link + ")"}}
                    className="bg-image-container"
                    onClick={this.handleOnClick}
                    title={this.props.image.title}
            >
                <svg viewBox={"0 0 " + (this.props.aspectRatio > 0 ? this.props.aspectRatio : 1) + " 1"}>
                    <rect width="100%" height="100%" fill="none"/>
                </svg>
            </figure>
        );
    }
}

export default SvgBgImage;