import React, {Component} from 'react';
import './GeneralSplashStyles.scss';
import { withRouter } from 'react-router-dom';
import Links from '../../Config/Links';
import Times from "../../Resources/Images/times-icon.png";
import Config from "../../Config/Config";


class BaseSplash extends Component {
    goBack = () => {
        if (document.referrer.indexOf(Config.contact.domain) !== -1) {
            window.history.go(-1);
        } else {
            this.props.history.push(Links.home);
        }
    };

    render() {
        return (
            <div className="component-full">
                <div className="exit-button" onClick={this.goBack}>
                    <img src={Times} alt="exit button"/>
                </div>

                {this.props.children}
            </div>
        )
    }
}

export default withRouter(BaseSplash);