import React, {Component} from 'react';
import './ThreeSteps.scss';
import ListIcon from "../../Resources/Images/list-icon.png";
import GlassesIcon from "../../Resources/Images/glasses-icon.png";
import StarsIcon from "../../Resources/Images/stars-icon.png";
import One from "../../Resources/Images/one-bg.png";
import Two from "../../Resources/Images/two-bg.png";
import Three from "../../Resources/Images/three-bg.png";


class ThreeSteps extends Component {
    render() {
        return(
            <div className="three-steps-component">
                <div className="number-container" 
                    style={{backgroundImage: "url(" + One + ")"}}> 
                    <div className="icon-container">
                        <img src={ListIcon} alt="" className="icon"/>
                    </div>

                    <div className="text-container">
                        <p className="title">Perfectly Planned Event</p>
                        <p className="text">The first step of a seamless event experience is the creative ideas that form an ideal event strategy.</p>
                    </div>
                </div>

                <div className="number-container"                   
                    style={{backgroundImage: "url(" + Two + ")"}}>
                    <div className="icon-container">
                        <img src={GlassesIcon} alt="" className="icon"/>
                    </div>

                    <div className="text-container">
                        <p className="title">Executed with Precision</p>
                        <p className="text">Even the smallest of details matter as they are the secret ingredients in creating truly magical events.</p>
                    </div>
                </div>

                <div className="number-container"                    
                    style={{backgroundImage: "url(" + Three + ")"}}>
                    <div className="icon-container">
                        <img src={StarsIcon} alt="" className="icon"/>
                    </div>
                    
                    <div className="text-container">
                        <p className="title">A Thorough Evaluation </p>
                        <p className="text">A key part of event management is reflecting on the success of said event in achieving its purpose.</p>
                    </div>
                </div>
            </div>
        );
    }
}

export default ThreeSteps;