import React, {Component} from 'react';
import './StaffTabs.scss';
import SuitIcon from "../../Resources/Images/suit-icon.png";
import DrinksIcon from "../../Resources/Images/hand-drinks-icon.png";
import WaiterIcon from "../../Resources/Images/waiter-icon.png";
import TeamIcon from "../../Resources/Images/collaborative-icon.png";
import Background from "../../Resources/Images/staff-tabs-bg.png";




class StaffTabs extends Component {
    state = {
        activeTab: 1,
    };

    changeTabActive = (e) => {
        var id = e.currentTarget.id;

        this.setState({
            activeTab: parseInt(id)
        });

        // Scroller.scrollTo('.dinamic-content-container', 500, 20);
    };

    render() {
        var icon = SuitIcon;
        var title = "Well-Presented";
        var text = "Waitstaff are essentially an extension of your event and will interact with everyone on your guestlist. All our staff are impeccably groomed with terrific people skills to ensure all guests get the 5-star treatment they deserve.";
        var golden1 = "golden-line" + (this.state.activeTab === 1 ? '' : ' hidden');
        var golden2 = "golden-line" + (this.state.activeTab === 2 ? '' : ' hidden');
        var golden3 = "golden-line" + (this.state.activeTab === 3 ? '' : ' hidden');
        var golden4 = "golden-line" + (this.state.activeTab === 4 ? '' : ' hidden');

        if (this.state.activeTab === 2) {
            icon = DrinksIcon;
            title = "Hospitality Intuition";
            text = "When it comes to hospitality one of the key skills is developing an intuition on how to provide exceptional service by anticipating events before they occur. Whatever situation arises, you can be sure our staff will have the composure and finesse to handle it well.";

        } else if (this.state.activeTab === 3) {
            icon = WaiterIcon;
            title = "Emotionally Connect";
            text = "Our waitstaff can help you establish an experience that engages your audience by creating long-lasting memories and good impressions. Our waitstaff will undergo a tailored training session so they understand your brand. This will help create personalised human interactions, that the digital world cant always cater for  making a huge difference for brand experience.";

        } else if (this.state.activeTab === 4) {
            icon = TeamIcon;
            title = "Anything is Possible";
            text = "In this changing landscape, SixthSense Agency never limits our capabilities to inside the box. We are more than willing to hear all your ideas and work out a way to bring it to life. Whatever you need for your event, we can help you find the perfect waitstaff.";
        }

        return (
            <div className="staff-tabs-component">
                <h2 className="staff-tabs-title">The best qualities in <br/>our waitstaff</h2>
                <div className="icons-tabs">
                    <div id="1" onClick={this.changeTabActive}
                         className="tab-container">
                        <img src={SuitIcon} alt="" className="top-icon"/>
                        <h3 className="icon-title">Well-Presented</h3>
                        <div className={golden1}></div>
                    </div>
                    <div id="2" onClick={this.changeTabActive}
                         className="tab-container">
                        <img src={DrinksIcon} alt="" className="top-icon"/>
                        <h3 className="icon-title">Hospitality Intuition</h3>
                        <div className={golden2}></div>
                    </div>
                    <div id="3" onClick={this.changeTabActive}
                         className="tab-container">
                        <img src={WaiterIcon} alt="" className="top-icon"/>
                        <h3 className="icon-title">Emotionally Connect</h3>
                        <div className={golden3}></div>
                    </div>
                    <div id="4" onClick={this.changeTabActive}
                         className="tab-container">
                        <img src={TeamIcon} alt="" className="top-icon"/>
                        <h3 className="icon-title">Anything is Possible</h3>
                        <div className={golden4}></div>
                    </div>
                </div>

                <div className="dinamic-content-container">

                    <div className="icon-container" style = {{backgroundImage: "url(" + Background + ")"}}>
                        <img src={icon} alt={1} className="dinamic-icon"/>
                    </div>
                    <div className="text-container">
                        <h3 className="content-title">{title}</h3>
                        <p className="content-text">{text}</p>
                    </div>


                </div>


            </div>
        );
    }
}

export default StaffTabs;