import React, {Component} from 'react';
import './Infopack.scss';

class Infopack2 extends Component {
    render() {
        return(
            <div className="infopack-component">               
                <p className="text">
                    {this.props.text}
                </p>

                {this.props.children}
            </div>
        )
    }
}

export default Infopack2;