import React, {Component} from 'react';
import './IconsTop.scss';
import HeadIcon from "../../Resources/Images/head-icon.png";
import GroupIcon from "../../Resources/Images/group-icon.png";
import WellIcon from "../../Resources/Images/wellness-icon.png";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";



class IconsTop extends Component {
    render(){
        return(
            <div className="icons-top-component">
                <div className="header">
                    <h2 className="title">Why Should You Join the <br/>SixthSense Family?</h2>
                </div>
                
                <div className="icons-container">
                    <div className="content-container">
                        <img src={HeadIcon} alt="Head icon" className="icon"/>
                        <p className="text">We promote a healthy balance between mind and body for all our <Link to={Links.staff}>staff</Link>. We care about how you feel, especially when youre working for us.</p>
                    </div>

                    <div className="content-container">
                        <img src={GroupIcon} alt="Group icon" className="icon"/>
                        <p className="text">You will learn how to take the customer experience to the next level by building an emotional connection through memorable moments and attentive customer service.</p>
                    </div>

                    <div className="content-container">
                        <img src={WellIcon} alt="hands and flower icon" className="icon"/>
                        <p className="text">We are committed to a focus on <Link to={Links.staff}>staff</Link> health and well-being as we know that a business will only thrive if its employees can too.</p>
                    </div>

                </div>

            </div>
        );
    }
}

export default IconsTop;