import React, {Component} from 'react';
import './Logo.scss';
import SixLines from '../SixLines/SixLines';

class Logo extends Component {
    render() {
        return (
            <div className="component-logo">
                <SixLines
                    horizontal={false}
                    bg_dark={false}
                    accent="all"
                />
            </div>
        );
    }
}

export default Logo;