import React, {Component} from 'react';
import TitleAccentBar from "../Misc/TitleAccentBar"
import HorizontalGallery from "../AboutPage/HorizontalGallery"
import FullScreenImage from "../MemoriesPage/FullScreenImage"
import Config from "../../Config/Config"
import './ExperienceGallery.scss';

class ExperienceGallery extends Component {
	state = {
		activeImage: null,
		images     : [],
		unmounted  : false
	};

	componentDidMount() {
		var baseUrl = Config.api.baseUrl + Config.api.galleryAlbumSlug;

		fetch(baseUrl + '?albumId=RArw7')
				.then(fetchResponse => fetchResponse.json())
				.then((jsonData) => {
					if (jsonData.result) {
						var images = jsonData.result.images;

						images.sort(function (image1, image2) {
							return image1.title > image2.title ? 1 : -1;
						});

						images.map((image) => {
							image.direct_link = 'https://' + image.direct_link;
							return image;
						});

						if (!this.state.unmounted) {
							this.setState({images: images})
						}
					}
				});
	}

	componentWillUnmount() {
		this.setState({
			unmounted: true
		})
	}

	onImageClick = (index) => {
		this.setState({
			activeImage: index
		});
	}

	onGalleryClose = (e) => {
		this.setState({
			activeImage: null
		})
	}

	render() {
		return (
				<div className="experience-gallery-container">
					<div className="subtitle-container">
						<h2 className="title-component">
							<div>Gallery</div>
							<TitleAccentBar/>
						</h2>
					</div>

					<div className="scrollme animateme"
					     data-when="enter"
					     data-from="0.5"
					     data-to="0"
					     data-translatex="500">
						{
							this.state.images.length
							?
							<HorizontalGallery images={this.state.images} onClick={this.onImageClick}/>
							:
							<div className="loading-spinner-outer">
								<div className="loading-spinner-inner"/>
							</div>
						}
					</div>
					{
						this.state.activeImage !== null
						?
						<FullScreenImage image={this.state.activeImage} images={this.state.images} onClose={this.onGalleryClose}/>
						:
						''
					}
				</div>
		);
	}
}

export default ExperienceGallery;