import React, {Component} from 'react';
import './imageWithText.scss';

class ImageWithTextRTL extends Component {
	render() {
		return (
				<div className="image-with-text-component rtl">


					<div className="text">
						<h3
								className="animateme scrollme"
								data-when="exit"
								data-from="0.7"
								data-to="0.1"
								data-easing="easeout"
								data-opacity="0">{this.props.title}</h3>
						<p
								className="animateme scrollme"
								data-when="exit"
								data-from="0.3"
								data-to="0"
								data-easing="easeout"
								data-opacity="0">{this.props.body}</p>
					</div>

					<div
							style={{backgroundImage: `url(${this.props.src})`}}
							title={this.props.title}
							className="image animateme scrollme"
							data-when="enter"
							data-from="0.5"
							data-to="0.3"
							data-scale="0.7"
							data-easing="easeout"
							data-opacity="0"
					/>
				</div>
		)
	}
}

export default ImageWithTextRTL;