import './Title.scss';
import React, {Component} from 'react';
import TitleAccentBar from "./TitleAccentBar";

class Title extends Component {
    render() {
        return (
            <h1 className="title-component">
                <div className="text">{this.props.text}</div>
                <TitleAccentBar/>
            </h1>
        );
    }
}

export default Title;