import React, {Component} from 'react';
import './StaffSplash.scss';
import StaffSplashImage from './StaffSplashImage';
import BaseSplash from "../SplashPages/BaseSplash";
import Submit from "../InputFields/SubmitButton";
import SPStaff from "../../Resources/Images/sp-staff.jpg";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";
import MetaTags from "react-meta-tags";




class StaffSplash extends Component {
    render() {
        return(
            <BaseSplash>
                <MetaTags>
                    <link rel="canonical" href={Links.baseUrl + Links.staff} />
                </MetaTags>
                <div className="component-staff-splash">
                    <div className="image-container">
                        <StaffSplashImage
                            imageSrc={SPStaff}
                            imageAlt="waiter smiling holding up a glass of champagne"/>
                    </div>

                    <div className="text-container">
                        <div className="title">Waitstaff <br/>Solutions</div>
                        <p className="text">Finding the right waitstaff can often be a 
                                    struggle. Let SixthSense match you with the perfect 
                                    staff for any event.</p>
                        <Link to={Links.staff}>
                            <Submit
                                label="Learn More"
                                isDark={true}
                            />
                        </Link>
                    </div>
                </div>
            </BaseSplash>
        )
    }
}
export default StaffSplash;