import React, {Component} from 'react';
import InnerPagesNav from '../Components/InnerPagesNav/InnerPagesNav';
import './ContentPage.scss';
import './ExperiencePage.scss';
import MetaTags from 'react-meta-tags';
import ImageWithTextLTR from "../Components/ImageWithText/ImageWithTextLTR"
import ImageWithTextRTL from "../Components/ImageWithText/ImageWithTextRTL"
import ThreeClocks from "../Components/Clocks/ThreeClocks"
import Infopack from "../Components/Infopack/Infopack"
import Footer from "../Components/Footer/Footer"
import Image1 from "../Resources/Images/experience-pic-1.jpg"
import Image2 from "../Resources/Images/experience-pic-3.jpg"
import Image3 from "../Resources/Images/experience-pic-2.jpg"
import TitleAccentBar from "../Components/Misc/TitleAccentBar"
import ExperienceGallery from "../Components/ExperiencePage/ExperienceGallery"
import BookExperienceButton
	from "../Components/ExperiencePage/BookExperienceButton"

class Experience extends Component {
	state = {
		logoVisible: true
	}

	resizeHeroVideo = () => {
		var container = document.getElementById('hero-video-container');
		if (container) {
			var containerAspectRatio = container.offsetWidth / container.offsetHeight;

			var video;

			var desktopVideo = document.getElementById('hero-iframe-desktop');
			var mobileVideo = document.getElementById('hero-iframe-mobile');
			// var heroBanner = document.getElementById('hero-banner');

			if (containerAspectRatio > 14 / 9) {
				video = desktopVideo;
				desktopVideo.style.display = 'initial';
				mobileVideo.style.display = 'none';
				// heroBanner.style.display = 'none';
			} else {
				video = mobileVideo;
				desktopVideo.style.display = 'none';
				mobileVideo.style.display = 'initial';
				// heroBanner.style.display = 'initial';
			}

			var videoAspectRatio = 16 / 9;

			if (containerAspectRatio > videoAspectRatio) {
				video.style.width = (container.offsetWidth) + 'px';
				video.style.height = (video.offsetWidth / videoAspectRatio) + 'px';
			} else {
				video.style.height = (container.offsetHeight) + 'px';
				video.style.width = (video.offsetHeight * videoAspectRatio) + 'px';
			}

			setTimeout(function () {
				var iframeContainer = document.getElementById('iframe-container');
				if (iframeContainer) {
					iframeContainer.style.marginLeft = '-' + Math.round(Math.abs(video.offsetWidth - container.offsetWidth) / 2) + 'px';
				}
			}, 5);
		}
	}

	componentDidMount() {
		setTimeout(function() {
			window.scrollTo(0, 0);
		}, 500);

		setTimeout(() => {
			this.setState({logoVisible: false})
		}, 2000);

		setTimeout(() => {
			setInterval(() => {
				this.resizeHeroVideo();
			}, 10)
		}, 1000)
	}

	render() {

		return (
				<div className="experience-page-component">
					<MetaTags>
						<title>Immersive Fine Dining Experience - SixthSense Agency</title>
						<meta name="description" content="Event management with SixthSense Agency uses the power of experience and intuition to bring you a seamless event experience that will set your senses alive."/>
					</MetaTags>

					<div className="nav-container">
						<div className="content-container-component">
							<InnerPagesNav mode={'dark'}/>
						</div>
					</div>

					<div className="hero-video-container" id="hero-video-container">
						<div id="iframe-container">
							<iframe className="hero-iframe" id="hero-iframe-mobile" src="https://player.vimeo.com/video/493659061?background=1" frameBorder="0" allow="autoplay; fullscreen" allowFullScreen title="Hero video mobile"/>
							<iframe className="hero-iframe" id="hero-iframe-desktop" src="https://player.vimeo.com/video/493658713?background=1" frameBorder="0" allow="autoplay; fullscreen" allowFullScreen title="Hero video desktop"/>
						</div>
					</div>

					<div className="hero-video-container2">
						<iframe className="hero-iframe" id="hero-iframe-desktop" src="https://player.vimeo.com/video/495663433?background=1" frameBorder="0" allow="autoplay; fullscreen" allowFullScreen title="Hero video mobile 2"/>
					</div>

					<div className="experience-content-container">

						<ThreeClocks />

						<div className="row mb-4">
							<div className="col-xs-12 col-lg-6 content-1"
							>

								<h1 className=" scrollme animateme"
								    data-when="enter"
								    data-from="0.8"
								    data-to="0.3"
								    data-opacity="0"
								    data-translatey="-200">
									Immersive Fine Dining Experience
								</h1>

								<p
										className="scrollme animateme"
										data-when="enter"
										data-from="0.8"
										data-to="0.3"
										data-opacity="0"
								>
									Be captivated and enchanted by our bespoke and
									totally customized immersive <span style={{'whiteSpace': 'nowrap'}}>multi-sensory</span> dining
									experience, designed to take fine dining
									to the next level. We harness sight, taste,
									sound, touch, and scent, aiming to create and
									frame a compelling and alluring narrative,
									leading to a deeply immersive emotional connection
									throughout the journey.
								</p>
								<p
										className="scrollme animateme"
										data-when="enter"
										data-from="0.8"
										data-to="0.3"
										data-opacity="0">
									We deliver a unique immersive three-course
									fine dining experience which combines technology,
									theatrical gastronomy and storytelling, designed
									to absorb and fascinate all your senses and faculties.
								</p>
								<p
										className="scrollme animateme"
										data-when="enter"
										data-from="0.8"
										data-to="0.3"
										data-opacity="0">
									Prepare to be entertained and intrigued by
									the dynamism and energy of an unimaginable
									experience, superb food, unmatched beverage,
									together with the most qualified waitstaff
									providing outstanding and personalized guest
									experiences and a world standard end-to-end
									service.
								</p>
								<br/>
								<p
										className="scrollme animateme"
										data-when="enter"
										data-from="0.8"
										data-to="0.3"
										data-opacity="0">
									<BookExperienceButton showPartner={true}/>
								</p>
							</div>

							<div className="video1-container col-xs-12 col-lg-6 scrollme animateme "
							     data-when="enter"
							     data-from="1"
							     data-to="0.2"
							     data-opacity="0"
							     data-translatex="1000"
							     data-translatey="-200"
							     data-rotatez="-20">
								<iframe
										src="https://player.vimeo.com/video/496832860?background=1"
										frameBorder="0"
										allow="autoplay; fullscreen"
										allowFullScreen
										className="shortVideo"
										title="videoProjection"
								/>
							</div>
						</div>

						<ImageWithTextLTR
								src={Image1}
								title="A storytelling adventure"
								body="Our production plays with stories of memory and
                            impermanence, <b>A Journey Through Time</b>, to remind
                            us we are not fixed, but in a constant state of
                            flow. We hope its artful blending of past, present
                            and future will stay with you and remind you the
                            principles of this wonderful life."
								body2="This experience includes the SixthSense
							main production, gastronomy, beverage and
							waitstaff; its fully customized, and can be
							brought to your preferred venue, office,
							yacht or private home."
						/>

						<ImageWithTextRTL
								src={Image2}
								title="Theatrical Gastronomy"
								body="At SixthSense, we believe that exquisitely
								delicious food nourishes the soul and each one of
								our senses, after all, taste is simply a construction
								of our mind. With magnificent food paired with
								stunning drinks, unique and luscious flavours
								and amazing presentation we create, not just
								excitement, but a truly interactive and engaged
								community of joyful customers feeling cheerful
								and contented whilst absorbing true immersion
								and personal mindfulness."
						/>


						<ImageWithTextLTR
								src={Image3}
								title="The Staff"
								body="Our staff are highly skilled, diligent and
								attentive to procuring the ultimate customer immersive
								experience. This is achieved through highly rigorous
								training processes designed to ensure that all staff
								are enthusiastic team members with a genuine
								willingness to excel at all levels of service delivery."
						/>

						<div className="promo-video-container">
							<div className="subtitle-container">
								<h2 className="title-component">
									<div>A Journey Through Time</div>
									<TitleAccentBar/>
								</h2>
							</div>

							<div style={{textAlign: 'center'}}>
								<iframe src="https://www.youtube.com/embed/SGKnmkqy8Yg" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen title="Promotional video"/>
								<br/>
								<br/>
								<br/>
								<br/>
								<BookExperienceButton showPartner={true}/>
							</div>

						</div>

						<div className="experience-gallery">
							<ExperienceGallery/>
						</div>

						<div className="lists-container">
							<div className="row">
								<div className="col-xs-12 col-md-6">
									<div className="subtitle-container ">
										<h2 className="title-component">
											<div>The production</div>
											<TitleAccentBar/>
										</h2>
									</div>

									<p><b>Includes:</b></p>

									<ul>
										<li>Up to 4 hours of immersive Storytelling show</li>
										<li>2D and 3D visuals on dining table</li>
										<li>Surround sound system</li>
										<li>Logistics, set up / pack down and delivery</li>
										<li>VJ Operator</li>
										<li>Dining table and chairs</li>
										<li>Linen, cutlery, crockery and glassware</li>
									</ul>

								</div>
								<div className="col-xs-12 col-md-6">
									<div className="subtitle-container">
										<h2 className="title-component">
											<div>The full experience</div>
											<TitleAccentBar/>
										</h2>
									</div>
									<p><b>Includes:</b></p>

									<ul>
										<li>The production</li>
										<li>Surrounding projections</li>
										<li>Catering</li>
										<li>Beverage</li>
										<li>Staff</li>
										<li>Venue</li>
									</ul>
								</div>
							</div>
						</div>

						<Infopack text="Immerse Your Experience With SixthSense Agency"/>
						<Footer/>
					</div>
				</div>
		);
	}
}

export default Experience;