import React from 'react';
import './Input.scss';
import './Select.scss';
import Input from "./Input";
import DropdownArrowImg from "./../../Resources/Images/dropdown_arrow.png"

class Select extends Input {
    state = {
        value    : '',
        open     : false,
        showLabel: false,
    };

    render() {
        var selectClass = (this.props.inputClass ? this.props.inputClass : '');
        selectClass += (this.props.isDark ? ' dark-bg' : '');
        selectClass += (this.state.invalid ? ' invalid' : '');

        var containerClass = "component-input component-select" + (this.state.showLabel ? ' show-label' : '');

        return (
            <div className={containerClass}>
                <label htmlFor={this.props.id}>{this.props.label ? this.props.label : this.props.placeholder}</label>
                <span style={{backgroundImage: 'url(' + DropdownArrowImg + ')'}} className={"dropdown-icon" + (this.state.open ? ' open' : '')}/>
                <select
                    className={selectClass}
                    name={this.props.name}
                    id={this.props.id}
                    onChange={this.handleValueChange}
                    onFocus={this.handleOnFocus}
                    onBlur={this.handleOnBlur}
                    required={!!this.props.required}
                >
                    <option value="">{this.props.label ? this.props.label : 'Please select one'}</option>

                    {this.props.options.map((option) => {
                        return (
                            <option
                                key={typeof option == 'object' ? (option.value ? option.value : option.label) : option}
                                value={typeof option == 'object' ? (option.value ? option.value : option.label) : option}
                                selected={option.selected}>
                                {typeof option == 'object' ? option.label : option}
                            </option>
                        );
                    })}
                </select>
            </div>
        );
    }

    handleValueChange = (evt) => {
        this.setState({open: false});
        let value = evt.target.value;
        this.setState({value: value});
        if (value) {
            this.setState({showLabel: true});
        } else {
            this.setState({showLabel: false});
        }
    };
    handleOnFocus = () => {
        this.setState({open: true})
    };
    handleOnBlur = () => {
        this.setState({open: false})
    };

}

export default Select;