import React, {Component} from 'react';
import './Input.scss';

class Input extends Component {
    state = {
        showLabel: false,
        invalid: false,
        value: '',
    };

    componentDidMount() {
        this.setState(this.props);
        if (this.props.value) {
            this.setState({showLabel: true});
        }
    }

    removeFocusedState = () => {
        if (!this.state.value) {
            this.setState({showLabel: false});
        }
    };

    setFocusedState = () => {
        this.setState({showLabel: true});
    };

    setInputValue = (evt) => {
        let value = evt.target.value;
        this.setState({value: value});
        if (value) {
            this.setState({showLabel: true});
        } else {
            this.setState({showLabel: false});
        }
    };

    render() {
        var inputClass = (this.state.inputClass ? this.state.inputClass : '');
        inputClass += (this.state.isDark ? ' dark-bg' : '');
        inputClass += (this.state.invalid ? ' invalid' : '');
        var containerClass = "component-input" + (this.state.showLabel ? ' show-label' : '');
        return (
            <div className={containerClass}>
                <label htmlFor={this.state.id}>{this.state.label ? this.state.label : this.state.placeholder}</label>
                <input
                    className={inputClass}
                    id={this.state.id}
                    name={this.state.name}
                    value={this.state.value}
                    type={this.state.type}
                    onClick={this.state.onclick}
                    required={this.state.required}
                    autoComplete={this.state.autocomplete}
                    onChange={this.setInputValue}
                    placeholder={this.state.placeholder}/>
            </div>
        );
    }
}

export default Input;