import React, {Component} from 'react';
import './MemoriesSplash.scss';
import MemoriesSplashImage from './MemoriesSplashImage';
import BaseSplash from "../SplashPages/BaseSplash";
import Submit from "../InputFields/SubmitButton";
import SPMemories from "../../Resources/Images/sp-memories.jpg";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";
import MetaTags from "react-meta-tags";





class MemoriesSplash extends Component {
    render() {
        return(
            <BaseSplash>
                <MetaTags>
                    <link rel="canonical" href={Links.baseUrl + Links.memories} />
                </MetaTags>
                <div className="component-memories-splash">
                    <div className="image-container">
                        <MemoriesSplashImage
                            imageSrc={SPMemories}
                            imageAlt="Chef pouring sauce over a steak in a plate"/>
                    </div>

                    <div className="text-container">
                        <div className="title">Memories &amp; <br/>Experiences</div>
                        <p className="text">Browse the SixthSense Agency gallery of memorable experiences to see 
                                        more of what we do and how we can make your next event truly shine.</p>
                        <Link to={Links.memories}>
                            <Submit
                                label="Learn More"
                                isDark={true}
                            />
                        </Link>
                    </div>
                </div>
            </BaseSplash>
        )
    }
}
export default MemoriesSplash;