import React, {Component} from 'react';
import './FourImg.scss';
import Cocktail1 from "../../Resources/Images/cocktail1.jpg";
import Cocktail2 from "../../Resources/Images/cocktail2.jpg";
import Cocktail3 from "../../Resources/Images/cocktail3.jpg";
import Cocktail4 from "../../Resources/Images/cocktail4.jpg";


class FourImg extends Component {
    render() {
        return (
            <div className="four-images-component">
                <div className="text-container">
                    <h2 className="title">Entertain Your Guests with Our Mobile Cocktail Masterclasses</h2>
                    <p className="text">If youre looking for an extra element to elevate your next event, consider a mobile cocktail masterclass with SixthSense. Our masterclasses are perfectly suited for any ocassion.</p>
                    <ul className="list">
                        <li>Corporate clients events</li>
                        <li>Team building</li>
                        <li>Intimate private parties</li>
                        <li>Interactive and fun atmosphere</li>
                        <li>Up to 25 persons</li>
                    </ul>
                </div>
                <div className="four-images-container">
                    <div className="four-images left">
                        <div className="image-container"
                            style={{backgroundImage: "url(" + Cocktail1 + ")"}}>
                            <svg viewBox="0 0 1 1" className="svg">
                                <rect
                                    width="1"
                                    height="1"
                                    fill="none"
                                />
                            </svg>
                        </div>
                    </div>
                    <div className="four-images">
                        <div className="image-container"
                            style={{backgroundImage: "url(" + Cocktail2 + ")"}}>
                            <svg viewBox="0 0 1 1" className="svg">
                                <rect
                                    width="1"
                                    height="1"
                                    fill="none"
                                />
                            </svg>
                        </div>
                    </div>
                    <div className="four-images left">    
                        <div className="image-container"
                            style={{backgroundImage: "url(" + Cocktail3 + ")"}}>
                            <svg viewBox="0 0 1 1" className="svg">
                                <rect
                                    width="1"
                                    height="1"
                                    fill="none"
                                />
                            </svg>
                        </div>
                    </div>
                    <div className="four-images">   
                        <div className="image-container"
                            style={{backgroundImage: "url(" + Cocktail4 + ")"}}>
                            <svg viewBox="0 0 1 1" className="svg">
                                <rect
                                    width="1"
                                    height="1"
                                    fill="none"
                                />
                            </svg>
                        </div>
                    </div> 
                </div>
            </div>

        );
    }
}
export default FourImg;