import React, {Component} from 'react';
import Input from "../InputFields/Input";
import TextArea from "../InputFields/TextArea";
import Submit from "../InputFields/SubmitButton";
import DangerAlert from "../Alerts/DangerAlert";
import SuccessAlert from "../Alerts/SuccessAlert";
import AjaxClient from "../../Utility/AjaxClient";
import Config from "../../Config/Config";

class ContactForm extends Component {
    state = {
        isValidForm: true,
        errorMessage: '',
        successMessage: '',
        disabled: false,
        waiting: false
    };

    constructor(props) {
        super(props);
        this.NameInput = React.createRef();
        this.PhoneInput = React.createRef();
        this.EmailInput = React.createRef();
        this.MessageInput = React.createRef();
        this.submitForm = this.submitForm.bind(this);
    }
    
    submitForm(event) {
        let valid = this.validateForm();
        event.preventDefault();
        if (valid) {
            this.setState({waiting: true});
            var data = {
                name: this.NameInput.current.state.value,
                email: this.EmailInput.current.state.value,
                phone: this.PhoneInput.current.state.value,
                message: this.MessageInput.current.state.value
            };
            AjaxClient.postAjax(
                Config.api.baseUrl + Config.api.contactSlug,
                data,
                (response) => {
                    this.setState({waiting: false});
                    const data = JSON.parse(response);
                    if (data.success === true) {
                        this.setSuccessMessage(data.message ? data.message : 'Your message was sent. Thank you.');
                        this.setState({disabled: true})
                    } else {
                        this.setErrorMessage(data.message ? data.message : 'There was an unexpected error. Please try again.');
                    }
                },
                () => {
                    this.setState({waiting: false});
                    this.setErrorMessage('There was an unexpected error. Please try again. If the problem persists, please write us an email to the email address found in this page.');
                }
            );
        }
    }

    validateForm() {
        this.resetErrorMessage();

        var name = this.NameInput.current.state;
        var email = this.EmailInput.current.state;
        name.invalid = false;
        email.invalid = false;

        if (!name.value || name.value === '') {
            this.setErrorMessage('Your name is required');
            name.invalid = true;
            return false;
        }

        if (!email.value || email.value === '') {
            this.setErrorMessage('Your email is required so we can get back to you');
            email.invalid = true;
            return false;
        }

        return true;
    }

    setErrorMessage(message) {
        this.setState({
            isValidForm : false,
            errorMessage: message
        });
    }

    setSuccessMessage(message) {
        this.setState({
            isValidForm : true,
            successMessage: message
        });
    }

    resetErrorMessage() {
        this.setState({
            isValidForm : true,
            errorMessage: '',
            successMessage: ''
        });
    }

    render() {
        var isDark = this.props.isDark;
        return (
            <form className="component-contact-form" onSubmit={this.submitForm}>
                <Input
                    placeholder="Name *"
                    name="name"
                    autocomplete="name"
                    required={true}
                    isDark={isDark}
                    ref={this.NameInput}
                />
                <Input
                    placeholder="Phone"
                    name="phone"
                    type="tel"
                    autocomplete="tel"
                    isDark={isDark}
                    ref={this.PhoneInput}
                />
                <Input
                    placeholder="Email *"
                    name="email"
                    type="email"
                    autocomplete="email"
                    required={true}
                    isDark={isDark}
                    ref={this.EmailInput}
                />
                <TextArea
                    placeholder="Message"
                    name="message"
                    type="email"
                    autocomplete="email"
                    required={true}
                    isDark={isDark}
                    rows="4"
                    ref={this.MessageInput}
                />
                {
                    !this.state.isValidForm
                        ? <DangerAlert msg={this.state.errorMessage}/>
                        : <SuccessAlert msg={this.state.successMessage}/>
                }
                <Submit
                    disabled={this.state.disabled || this.state.waiting}
                    label={this.state.waiting ? 'Sending' : 'Send'}
                    isDark={isDark}
                />
            </form>
        );
    }
}

export default ContactForm;