import React, {Component} from 'react';
import './AboutSplash.scss';
import AboutSplashImage from './AboutSplashImage';
import SPAbout from "../../Resources/Images/sp-about.jpg";
import BaseSplash from "../SplashPages/BaseSplash";
import Submit from "../InputFields/SubmitButton";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";
import MetaTags from "react-meta-tags";





class AboutSplash extends Component {
    render() {
        return (
            <BaseSplash>
                <MetaTags>
                    <link rel="canonical" href={Links.baseUrl + Links.about} />
                </MetaTags>
                <div className="component-about-splash">
                    <div className="image-container">
                        <AboutSplashImage
                        imageSrc={SPAbout}
                        imageAlt="pink cocktail with orange peel served in a tall glass"/>
                    </div>

                    <div className="text-container">
                        <div className="title">About<br/> SixthSense</div>
                        <p className="text">
                            The SixthSense Agency provide experienced event management
                            and waitstaff solutions for both corporate and private
                            events across Sydney.
                        </p>

                        <Link to={Links.about}>
                            <Submit
                                label="Learn More"
                                isDark={true}
                            />
                        </Link>
                   </div>
                </div>
            </BaseSplash>
        );
    }
}
export default AboutSplash;