import React, {Component} from 'react';
import './BookExperienceButton.scss';
import PartnerLogo from "../../Resources/Images/booking-partner-logo.png"

class BookExperienceButton extends Component {

	render() {
		return (
				<span className='book-experience-container'>
					<button className="book-experience-button"
					   id={"eventbrite-widget-modal-trigger-377682988987"}>
						Book now!
					</button>
					{
						this.props.showPartner
						? (
								<span className='partner-container'>
									<img src={PartnerLogo} alt="ShuffleMedia"/>
								</span>
						)
						: ''

					}
				</span>
		);
	}
}

export default BookExperienceButton;