import React from 'react';
import './SubmitButton.scss';
import Input from "./Input";

class SubmitButton extends Input {
    state = {
        disabled: false,
    };

    render() {
        var buttonClass = (this.props.inputClass ? this.props.inputClass : '');
        buttonClass += (this.props.isDark ? ' dark-bg' : '');
        return (
            <div className="component-submit-button">
                <button
                    type="submit"
                    className={buttonClass}
                    id={this.props.id}
                    onClick={this.props.onclick}
                    disabled={this.props.disabled}
                    title={this.props.disabled ? 'Disabled' : this.props.label}
                >{this.props.label}</button>
            </div>
        );
    }
}

export default SubmitButton;