import React, {Component} from 'react';
import './Infopack.scss';
import { Link } from 'react-router-dom';
import Links from '../../Config/Links';
import Submit from "../InputFields/SubmitButton";



class Infopack extends Component {
    render() {
        return(
            <div className="infopack-component">
                <p
                    className="text"
                    dangerouslySetInnerHTML={{__html: this.props.text ? this.props.text : 'Contact us for more information'}}
                />
                <Link to = {Links.contact}>
                    <Submit
                        label="Contact"
                        isDark={true}
                    />
                </Link>
            </div>
        )
    }
}

export default Infopack;