import React, {Component} from 'react';
import './JobsSplash.scss';
import JobsSplashImage from './JobsSplashImage';
import BaseSplash from "../SplashPages/BaseSplash";
import Submit from "../InputFields/SubmitButton";
import SPJobs from "../../Resources/Images/sp-jobs.jpg";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";
import MetaTags from 'react-meta-tags';


                        


class JobsSplash extends Component {
    render() {
        return(
            <BaseSplash>
                <MetaTags>
                    <link rel="canonical" href={Links.baseUrl + Links.jobs} />
                </MetaTags>
                <div className="component-jobs-splash">
                    <div className="image-container">
                        <JobsSplashImage
                            imageSrc={SPJobs}
                            imageAlt="Waiter in the background serving champagne"/>
                    </div>

                    <div className="text-container">
                        <div className="title">Jobs</div>
                        <p className="text">Are you looking for a job in hospitality? 
                            Work with SixthSense to create
                            unforgettable moments and memories.</p>
                        <Link to={Links.jobs} >
                            <Submit
                                label="Learn More"
                                isDark={true}
                            />
                        </Link>
                    </div>
                </div>
            </BaseSplash>
        )
    }
}
export default JobsSplash;