import React, {Component} from 'react';
import Footer from '../Components/Footer/Footer';
import InnerPagesNav from '../Components/InnerPagesNav/InnerPagesNav';
import './ContentPage.scss';
import SixImages from '../Components/StaffPage/SixImages';
import Infopack from '../Components/Infopack/Infopack';
import StaffTabs from '../Components/StaffPage/StaffTabs';
import StartChat from '../Components/StaffPage/StartChat';
import StaffHeader from '../Components/StaffPage/StaffHeader';
import MetaTags from 'react-meta-tags';

class Staff extends Component {
    componentDidMount() {
        setTimeout(function() {
            window.scrollTo(0, 0);
        }, 500);
    }
    render() {
        return (
            <div className="staff-page-component">
                <MetaTags>
                    <title>Waitstaff Solutions - SixthSense Agency</title>
                    <meta name="description" content="Finding the right waitstaff can often be a struggle. Let SixthSense match you with the perfect staff for any event. Get in touch with us today! "/>
                </MetaTags>

                <div className="content-container-component">
                    <InnerPagesNav/>
                    <StaffHeader/>
                    <StaffTabs/>
                </div>

                <div className="content-container-full">
                    <SixImages/>
                </div>
                <div className="content-container-component">
                    <StartChat/>
                    <Infopack text="Let SixthSense match you with the perfect waitstaff"/>
                    <Footer/>
                </div>


            </div>
        );
    }
}

export default Staff;