import React, {Component} from 'react';
import Footer from '../Components/Footer/Footer';
import InnerPagesNav from '../Components/InnerPagesNav/InnerPagesNav';
import './ContentPage.scss';
import TextLeftImage from '../Components/JobsPage/TextLeftImage';
import IconsTop from '../Components/JobsPage/IconsTop';
import MetaTags from 'react-meta-tags';
import Links from "../Config/Links";
import Infopack2 from "../Components/Infopack/Infopack2";
import Submit from "../Components/InputFields/SubmitButton";
import JobsForm from "../Components/JobsApplication/JobsForm";
import {Link} from "react-router-dom";

class Jobs extends Component {
    componentDidMount() {
        setTimeout(function() {
            window.scrollTo(0, 0);
        }, 500);
    }
    render() {
        return (
            <div className="jobs-page-component">
                <MetaTags>
                    <title>Jobs - SixthSense Agency</title>
                    <meta name="description" content="Are you looking for a job in hospitality? Work with SixthSense to create unforgettable moments and memories."/>
                </MetaTags>

                <div className="content-container-component">
                    <InnerPagesNav/>
                </div>

                <div className="content-container-full">
                    <TextLeftImage/>
                </div>

                <div className="content-container-component">
                    <IconsTop/>
                    <JobsForm />
                </div>

                <div className="content-container-component">
                    <Infopack2 text="Work culture and staff are our most valuable assets">
                        <Link to={Links.contact}>
                            <Submit label="Contact us" isDark={true} />
                        </Link>
                    </Infopack2>
                    <Footer/>
                </div>
            </div>
        );
    }
}

export default Jobs;