import React, {Component} from 'react';
import './SixIcons.scss';
import GlassesIcon from "../../Resources/Images/glasses-icon.png";
import MouthIcon from "../../Resources/Images/mouth-icon.png";
import HappyIcon from "../../Resources/Images/happiness-icon.png";
import FoodIcon from "../../Resources/Images/food-icon.png";
import TeamIcon from "../../Resources/Images/team-icon.png";
import LogoIcon from "../../Resources/Images/logo-icon.png";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";


class SixIcons extends Component {
    render() {
        
        return (
            <div className="six-icons-component">
                <div className="six-icon-containers">
                    <div className="icon-text-container">
                        <img className="icon" src={GlassesIcon} alt="Glasses icon"/>
                        <h3 className="title">The <span>sound</span> of champagne glasses</h3>
                        <p className="text">A seamless event is a rare and beautiful thing. Its decorated with the sound of laughter and champagne glasses clinking.</p>
                    </div>
                    <div className="icon-text-container">
                        <img className="icon" src={MouthIcon} alt="Mouth icon"/>
                        <h3 className="title">An explosion in your <span>mouth</span></h3>
                        <p className="text">Surprising flavours that blend together to create harmony  a feeling that comes with a truly memorable experience.</p>
                    </div>
                    <div className="icon-text-container">
                        <img className="icon" src={HappyIcon} alt="Happiness icon"/>
                        <h3 className="title">A feast for the <span>eyes</span></h3>
                        <p className="text">Drink in the sights of a beautifully decorated venue, delicious food and your guests dressed in their best outfits.</p>
                    </div>
                    <div className="icon-text-container">
                        <img className="icon" src={FoodIcon} alt="Food icon"/>
                        <h3 className="title">The Aromatic, Enticing <span>Scent</span> of Delicate Canapes</h3>
                        <p className="text">In every culture, food sparks joy, love and warmth. SixthSense serves up food that speaks to the soul.</p>
                    </div>
                    <div className="icon-text-container">
                        <img className="icon" src={TeamIcon} alt="Team icon"/>
                        <h3 className="title"><span>Feel</span> the buzz of excitement in the air</h3>
                        <p className="text">The light chatter between friends, soothing music as an undertone and  quality waitstaff who meet your needs before you anticipate them.</p>
                    </div>
                    <div className="icon-text-container">
                        <img className="icon" src={LogoIcon} alt="SixthSense logo icon"/>
                        <h3 className="title">Solutions <span>suited</span> perfectly for you</h3>
                        <p className="text">Each event is its own memory with different unique experiences. We find <Link to={Links.staff}>staff</Link> that synchronise their rhythms with clients to create impressionable <Link to={Links.memories}>memories</Link>.</p>
                    </div>

                </div>

                
            </div>
        );
    }
}

export default SixIcons;