import React, {Component} from 'react';
import './MemoriesGallery.scss';
import Config from "../../Config/Config";
import Gallery from "./Gallery";
import Title from "../Misc/Title";

class MemoriesGallery2 extends Component {
    state={
        images: []
    };

    componentDidMount() {

        var baseUrl = Config.api.baseUrl + Config.api.galleryAlbumSlug;

        fetch(baseUrl + '?albumId=ulUH')
            .then(fetchResponse => fetchResponse.json())
            .then((jsonData) => {
                if (jsonData.result) {
                    var images = jsonData.result.images;
                    images.sort(function(image1, image2) {
                        return image1.title > image2.title ? 1 : -1;
                    });

                    images.map((image) => {
                        image.direct_link = 'https://' + image.direct_link;
                        return image;
                    });

                    if (!this.state.unmounted) {
                        this.setState({images: images})
                    }
                }
            });
    }

    componentWillUnmount() {
        this.setState({
            unmounted: true
        })
    }

    render() {
        return (
            <div className="component-memories-gallery">
                <div className="gallerie2-title">
                    <Title text="Events that tell a story" />
                </div>
                <Gallery images={this.state.images}/>
            </div>
        );
    }
}

export default MemoriesGallery2;