import React, {Component} from 'react';
import './TextLeftImage.scss';
import Waiters from "../../Resources/Images/two-waiters.jpg";
import Title from '../Misc/Title';
import Background1 from "../../Resources/Images/jobs-sq.png";
import Background2 from "../../Resources/Images/jobs-bg.png";


class TextLeftImage extends Component {

    render() {
        return (
            <div className="component-text-left-image" style={{backgroundImage: "url(" + Background2 + ")"}}>
                <div className="medium-size-component">
                    <div className="jobs-title" style={{backgroundImage: "url(" + Background1 + ")"}}>
                        <Title text="Work with Us to Create Moments &amp; Memories" />
                    </div>
                    
                    <div className="text-left-container">
                        <p className="text">SixthSense Agency is always on the lookout for new talent to join our family. If youre passionate about the hospitality industry &amp; customer service and are looking to grow your skills with a team of like-minded individuals, feel free to drop us your resume.</p>
                        <p className="text">Our philosophy is based on the ability to anticipate events before they occur. That is the skill you will master when you become part of our team.</p>
                    </div>
                    <div className="image-right-container"
                            style={{backgroundImage: "url(" + Waiters + ")"}}>
                            <svg viewBox="0 0 1 0.85" className="svg">
                                <rect
                                    width="1"
                                    height="0.85"
                                    fill="none"
                                />
                            </svg>
                    </div>
                </div>
                
            </div>
                    
        );
    }
}

export default TextLeftImage;