import React, {Component} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebookF, faInstagram, faLinkedinIn, faYoutube } from '@fortawesome/free-brands-svg-icons';
import './SocialMedia.scss';
import Links from "../../Config/Links";

class SocialMedia extends Component {
    render() {
        return (
            <nav className= "component-social-media">
                <div className="">
                    <ul className="links-list">
                        <li className= "links-container">
                            <a href={Links.instagram} target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faInstagram} />  </a>
                        </li>
                        <li className= "links-container">
                            <a href={Links.facebook} target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faFacebookF} />  </a>
                        </li>
                        <li className= "links-container">
                            <a href={Links.linkedin} target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faLinkedinIn} /> </a>
                        </li>
                        <li className= "links-container">
                            <a href={Links.youtube} target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faYoutube} /> </a>
                        </li>
                    </ul> 
                </div>
            </nav>
        );
    }
}

export default SocialMedia;