import React, {Component} from 'react';
import './AboutSplashImage.scss';

class AboutSplashImage extends Component {
    state={};

    componentDidMount() {
        this.setState(this.props);  
    }

    render() {
        return (
            <div className="component-about-splash-image">
                <div className="svg-container">
                    <svg viewBox="0 0 51 51" className="svg">
                        <g>
                            <rect
                                    className="accent-bg"
                                    width="6"
                                    height="24"
                                    x="0"
                                    y="0"/>
                            <rect
                                    className="accent-bg"
                                    width="6"
                                    height="51"
                                    x="9"
                                    y="0"/>
                            <rect
                                    className="accent-bg"
                                    width="6"
                                    height="51"
                                    x="18"
                                    y="0"/>
                            <rect
                                    className="accent-bg"
                                    width="6"
                                    height="51"
                                    x="27"
                                    y="0"/>
                            <rect
                                    className="accent-bg"
                                    width="6"
                                    height="51"
                                    x="36"
                                    y="0"/>
                            <rect
                                    className="accent-bg"
                                    width="6"
                                    height="51"
                                    x="45"
                                    y="0"/>
                        </g>
                    </svg>
                
                </div>
                <div className="image-container">
                    <img src={this.state.imageSrc} alt={this.state.imageAlt} className="image-splash"></img>
                </div>
                <div className="empty-square-container">
                   <svg viewBox="0 0 51 51">
                        <g>
                            <rect
                                className="empty-square"
                                width="51"
                                height="51"
                                x="0"
                                y="0"
                            />
                        </g>
                    </svg>
                </div>
            </div>
        );
    }
}

export default AboutSplashImage;