import React, {Component} from 'react';
import './AboutHeader.scss';
import Title from "../Misc/Title";
import Background from "../../Resources/Images/about-bg.png";
import Links from "../../Config/Links";
import {Link} from "react-router-dom";

class AboutHeader extends Component {
    render() {
        return (

            <div className="about-header-component">
                <div className="content-container" style={{backgroundImage: "url(" + Background + ")"}}>
                    <div className="title">
                        <Title text="Hospitality services that anticipate your every need"/>
                    </div>

                    <p className="text">
                        At SixthSense Agency, we provide <Link to={Links.events}>event management</Link> and
                        <Link to={Links.staff}> waitstaff solutions</Link> throughout
                        Sydney designed to set your senses alive. Our purpose is
                        to provide personalised waitstaff and event solutions
                        suited perfectly to your unique requirements.
                    </p>
                </div>
            </div>

        );
    }
}

export default AboutHeader;