import React, {Component} from 'react';
import './Footer.scss';
import Logo from "../Logo/Logo";
import SocialMedia from "../SocialMedia/SocialMedia";
import Links from '../../Config/Links';
import Config from '../../Config/Config';
import {Link, NavLink} from 'react-router-dom';


class Footer extends Component {
    render() {
        return (
            <nav className="component-footer">
                <div className="links-container">
                    <div className="logo-container">
                        <Link to = {Links.home}>
                            <Logo/>
                        </Link>
                    </div>

                    <div className ="social-media-container">
                        <SocialMedia/>
                    </div>
            
                    <ul className="links-list">
                        {Config.navigation.map((link) => {
                            return (
                                    <li className="link-container" key={link.link}>
                                        <NavLink className="navigation-link" to={link.link}>
                                            {link.text}
                                        </NavLink>
                                    </li>
                            );
                        })}
                    </ul>
                </div>

                <div className="copyrigth-container">
                    <p>All rights reserved &copy; 2021 SixthSense Agency</p>
                </div>
            </nav>
        );
    }
}

export default Footer;