import './Title.scss';
import React, {Component} from 'react';

import SPAbout from "../../Resources/Images/sp-about.jpg";
import SPEvents from "../../Resources/Images/sp-events.jpg";
import SPJobs from "../../Resources/Images/sp-jobs.jpg";
import SPMemories from "../../Resources/Images/sp-memories.jpg";
import SPStaff from "../../Resources/Images/sp-staff.png";
import RWImage from "../../Resources/Images/red-wine.jpg";
import Waiters from "../../Resources/Images/two-waiters.jpg";

class ImagePreloader extends Component {
    render() {
        let styles = {
            height: '0px',
            overflow: 'hidden'
        };
        return (
            <div style={styles}>
                <img src={SPAbout} alt=""/>
                <img src={SPEvents} alt=""/>
                <img src={SPJobs} alt=""/>
                <img src={SPMemories} alt=""/>
                <img src={SPStaff} alt=""/>
                <img src={RWImage} alt=""/>
                <img src={Waiters} alt=""/>
            </div>
        );
    }
}

export default ImagePreloader;