import React, {Component} from 'react';
import './Mission.scss';
import Comillas from "../../Resources/Images/comillas.png";
import Next from "../../Resources/Images/next.png";
import Previous from "../../Resources/Images/previous.png";




class Mission extends Component {

    state ={
        activeQuote:0,

        quotes: [
            
            {
                text:"I have always known Cesar to be a person full of passion, who lives and breathes the values of hospitality.",
                text2: "Cesar is always looking for opportunities to go above and beyond to meet his client's expectations. His wealth of knowledge in the industry gives him the edge.",
                name:"Ransdale Dinger",
                name2: "Hospitality Services Manager NSW KPMG",
            },
            {
                text:"Cesar is professional, committed, hard working and not afraid of a challenge and I have every confidence that he will use the skills he has developed over the years to make SixthSense Agency a great success adding value to the industry.",
                name:"James Walkden",
                name2:"Area Director of Operations - Australia, New Zealand and Pacific Marriott International",
            },
            {
                text:"Cesar has always displayed the highest degree of integrity, responsibility, and professionalism. He is definitely a leader in his field.",
                text2: "He has proven his leadership ability by organizing and pioneering the highest quality hospitality service team within the corporate industry.",
                name:"ALI AKL",
                name2: "CEO, Tobys Estate Coffee Retail Group"
            },
            {
                text:"Cesar is a person of the utmost integrity, work ethic and honesty.",
                text2: "Through his passion he always provides his customers, clients, with exceptional service and I believe SixthSense Agency will become the industry benchmark, and market leader.",
                name:"Stephen Morahan",
                name2: "General Manager The Tasman - A luxury Collection Hotel"
            },
            {
                text:"The reason for our success is no secret. It's the Golden Rule  the simple idea that we should treat others the way we would want to be treated.",
                name:"Cesar Ramirez",
            },
        ]
    }

    nextQuote = (e) => {
        var currentQuote = this.state.activeQuote;
        var nextQuote = currentQuote + 1;
        if (nextQuote === this.state.quotes.length) {
            nextQuote = 0;
        }
        this.setState({
            activeQuote: nextQuote
        })
    };

    previousQuote = (e) => {
        var currentQuote = this.state.activeQuote;
        var nextQuote = currentQuote - 1;
        if (nextQuote < 0) {
            nextQuote = this.state.quotes.length - 1;
        }
        this.setState({
            activeQuote: nextQuote
        })
    };

    render() {
        
        var quote = this.state.quotes[this.state.activeQuote];

        return(
            <div className="mission-component">
                <div className="mission-text-container">
                    <h2 className="title">Our Mission</h2>
                    <p className="text">Our mission is to offer a seamless end to end experience to clients by embracing the art of intuition and anticipating their every need  we call this SixthSense hospitality.</p>
                    <p className="text">We are also committed to sustainability and staff wellbeing in the workplace; promoting a healthy balance between body, mind and our environment.</p>
                </div>
                <div className="squares-container">
                    <div className="text-container">
                        <img src={Comillas} alt="quotes-icon"/>
                        <div className="inner-text-container">
                            <p>{quote.text}</p>
                            <p>{quote.text2}</p>
                            <p className="quote-name-top">{quote.name}</p>
                            <p className="quote-name-bottom">{quote.name2}</p>
                        </div>
                        <span className="change-button next-button"
                            onClick={this.nextQuote}><img src={Next} alt="Next icon"/></span>
                        <span className="change-button previous-button"
                            onClick={this.previousQuote}><img src={Previous} alt="Previous icon"/></span>

                    </div>

                </div>

            </div>
        );

    }
}

export default Mission;