import React, {Component} from 'react';
import './ThreeClocks.scss';
import ClockLeft from '../../Resources/Images/clock-left.png';
import ClockTop from '../../Resources/Images/clock-top.png';
import ClockLeftCounter from '../../Resources/Images/clock-left-counter.png';

class ThreeClocks extends Component {
    render() {
        return (
                <div className="component-three-clocks scrollme">
                    <div
                            className="text-center clock-container animateme" data-when="enter"
                            data-from="0.5"
                            data-to="0.2"
                            data-opacity="0"
                            // data-translatex="-200"
                    >
                        <img src={ClockLeft} className="clock-1" alt=""/>
                    <br/>
                    What is the future?
                </div>
                    <div
                            className="text-center clock-container animateme"
                            data-when="enter"
                            data-from="0.6"
                            data-to="0.3"
                            data-opacity="0"
                            // data-translatex="-200"
                    >
                        <img src={ClockTop} className="clock-2" alt=""/>
                    <br/>
                    The present moment
                </div>
                    <div
                            className="text-center clock-container animateme"
                            data-when="enter"
                            data-from="0.7"
                            data-to="0.4"
                            data-opacity="0"
                            // data-translatex="-200"
                    >
                        <img src={ClockLeftCounter} className="clock-3" alt=""/>
                    <br/>
                    The past, how long, is long gone?
                </div>
            </div>
        );
    }
}

export default ThreeClocks;