import React, {Component} from 'react';
import Footer from '../Components/Footer/Footer';
import InnerPagesNav from '../Components/InnerPagesNav/InnerPagesNav';
import './ContentPage.scss';
import HorizontalGallery from '../Components/AboutPage/HorizontalGallery';
import SixIcons from '../Components/AboutPage/SixIcons';
import CesarContent from '../Components/AboutPage/CesarContent';
import Infopack from '../Components/Infopack/Infopack';
import Mission from '../Components/AboutPage/Mission';
import AboutHeader from '../Components/AboutPage/AboutHeader';
import MetaTags from 'react-meta-tags';
import Image1 from "../Resources/Images/sp-events.jpg";
import Image2 from "../Resources/Images/waiter-champagne.jpg";
import Image3 from "../Resources/Images/sp-about.jpg";
import Image4 from "../Resources/Images/sp-memories.jpg";
import Image5 from "../Resources/Images/staff-six2.jpg";


class About extends Component {
    componentDidMount() {
        setTimeout(function() {
            window.scrollTo(0, 0);
        }, 500);
    }

    render() {
        var galleryImages = [
            {direct_link: Image1},
            {direct_link: Image2},
            {direct_link: Image3},
            {direct_link: Image4},
            {direct_link: Image5},
        ];
        return (
            <div className="about-page-component">
                <MetaTags>
                    <title>About SixthSense - SixthSense Agency</title>
                    <meta name="description" content="The SixthSense Agency provide experienced intuitive event management and waitstaff solutions for both corporate and private events across Sydney."/>
                </MetaTags>
                <div
                    className="content-container-component">
                    <InnerPagesNav/>
                </div>
                <div className="content-container-full">
                    <AboutHeader/>
                </div>
                <div
                    className="content-container-component">
                    <SixIcons/>
                    <CesarContent/>
                </div>
                <div className="content-container-full">
                    <Mission/>
                    <HorizontalGallery images={galleryImages}/>
                </div>

                <div
                    className="content-container-component">
                    <Infopack text="Dedicated in providing seamless experiences for every client from start to finish"/>
                    <Footer/>
                </div>
            </div>
        );
    }
}

export default About;