import React, {Component} from 'react';
import './FullScreenImage.scss';
import closeIcon from './../../Resources/Images/times-icon.png'
import nextIcon from './../../Resources/Images/next.png'
import previousIcon from './../../Resources/Images/previous.png'

class FullScreenImage extends Component {
    state = {
        activeImage: null
    };

    constructor(props) {
        super(props);
        if (props.image >= 0) {
            this.state = {
                activeImage: props.image
            };
        }
    };

    handleClose = (e) => {
        this.setState({
            activeImage: null
        });
        if (typeof this.props.onClose === 'function') {
            this.props.onClose(e);
        }
    };

    render() {
        var activeImage = this.props.images[this.state.activeImage];

        if (activeImage) {
            var imageUrl = activeImage.direct_link;
        } else {
            return '';
        }

        var hash = this.getHash();
        this.setFocus();
        return (
            <div id={hash}
                 className="component-full-screen-gallery"
                 tabIndex={0}
                 onScroll={this.stopPropagation}
                 onClick={this.handleClose}
                 onKeyDown={this.handleKeyDown}>
                <div className="container">
                    <span className="close-button">
                        <img src={closeIcon} alt=""/>
                    </span>
                </div>
                <figure className="full-screen-gallery-figure" title={activeImage.title} style={{backgroundImage: 'url(' + imageUrl + ')'}} onClick={this.stopPropagation}/>
                <span className="change-button next-button"
                      onClick={this.nextImage}>
                    <img src={nextIcon} alt=""/>
                </span>
                <span className="change-button previous-button"
                      onClick={this.previousImage}>
                    <img src={previousIcon} alt=""/>
                </span>
            </div>
        );
    }

    stopPropagation = (e) => {
        e.preventDefault();
        e.stopPropagation();
    };

    nextImage = (e) => {
        this.stopPropagation(e);
        var currentImage = this.state.activeImage;
        var nextImage = (currentImage * 1) + 1;
        if (nextImage === this.props.images.length) {
            nextImage = 0;
        }
        this.setState({
            activeImage: nextImage
        })
    };

    previousImage = (e) => {
        this.stopPropagation(e);
        var currentImage = this.state.activeImage;
        var nextImage = (currentImage * 1) - 1;
        if (nextImage < 0) {
            nextImage = this.props.images.length - 1;
        }
        this.setState({
            activeImage: nextImage
        })
    };

    handleKeyDown = (e) => {
        switch (e.keyCode) {
            case 39: // Arrow right
                this.nextImage(e);
                break;
            case 37: // Arrow left
                this.previousImage(e);
                break;
            case 27: // Escape key
                this.handleClose(e);
                break;
            default:
                break;
        }

        this.setFocus();
    };

    setFocus = () => {
        setTimeout(() => {
            var hash = this.getHash();
            var element = document.getElementById(hash);
            if (element) {
                element.focus()
            }
        }, 100);
    };

    getHash() {
        var hash = '';
        this.props.images.forEach(function (image) {
            hash += image.id;
        });
        return hash;
    }
}

export default FullScreenImage;